/**
 * @fileOVerview trs reportlist page
 */
import * as React from 'react';
import { connect } from 'react-redux';
import { createSelector, BaseComponent, HTMLManager } from 'razy/dist/lib';
import * as _expressStatic from 'express-serve-static-core';
import * as homeActions from '../../a-action/home';
import * as Immutable from 'immutable';
import { browserHistory } from 'react-router';
import { ROUTE_PATH } from '../../routes';
import REQUEST from '../../const/request';


const style = require('./index.less');

class Reportlist extends BaseComponent<{}, {
    user_name: any,
    total_score: any,
    exam_title: any,
    exam_date: any,
    eid:any
}>{
    async interceptor(req: _expressStatic.Request, res: _expressStatic.Response, next: _expressStatic.NextFunction): Promise<any> { }
    setUpPage(manager: HTMLManager) { }
    getInitDataActionImp(props: any): void | any[] { }

    constructor(props: any) {
        super(props);
        this.state = {
            user_name: '',
            total_score: 0,
            exam_title: '',
            exam_date: '',
            eid:0

        }
    }


    async componentDidMount() {
        super.componentDidMount();
        const { dispatch } = this.props;

        //    发送请求
        try {
            let response = await _http.post(REQUEST.SUBMIT, { id: this.props.params.id });
            console.log(response[0]);
            var d = new Date(response[0].exam_date);
            var day = d.getDate();
            var month = d.getMonth() + 1;
            var year = d.getFullYear();
            var fullYear=year+'-'+month+'-'+day
            
            this.setState({
                user_name: response[0].user_name,
                total_score: response[0].total_score,
                exam_title: response[0].exam_title,
                exam_date: fullYear,
                eid:response[0].eid


            });

        } catch (error) {

        }
    }
    render() {

        return (
            <div>
                <style dangerouslySetInnerHTML={{ __html: style }}></style>
                <div className="s-header" >
                    <div className="ex-header-logo">
                        <span className="ex-header-logo-text">学而思TRS</span>
                    </div>

                </div>
                <div className="listContainer">
                    <div className="usrInfo">你好，{this.state.user_name}！欢迎来到TRS！</div>
                    <div id="reportList">
                        <h3 className="reportTitle">报告列表</h3>
                        <a href={"/report/"+this.props.params.id+"/"+this.state.eid +"/overall"}className="link active" >
                            <div className="reportSec">
                                <h3 className="rName">{this.state.exam_title}</h3>
                                <div className="rTime">{this.state.exam_date}</div>
                                <div className="reportBottom">
                                    <div className="rScore">得分 <span>{this.state.total_score}</span></div>
                                    <div className="rToDetail">点击查看详细报告</div>
                                </div>
                            </div>
                        </a>
                    </div>
                    <div className="shareInfo">
                        <img src={ret(`${__IMAGE_STATIC_PATH__}/Wechatshare.png`)} />
                    </div>
                </div>
                <footer>Copyright © 2017 南京学而思市场部</footer>
            </div>
        )

    }



}

const selector = () => ({});

export = connect(selector)(Reportlist);



